<template>
  <div id="login_bigBox" class="">
    <div class="txal mg-t-30" v-show='!this.$store.state.isLogin'>
      <div class="f20 padd-25 title">
        请填写你的个人信息<span class="f12">（虽然要不要都无所谓~）</span>
      </div>
      <div class="dslb" style="width: 60%;">
        <div class="fsa">
          <el-form :model="ruleForm" @submit.native.prevent :rules="rules" ref="ruleForm" hide-required-asterisk label-width="100px" class="demo-ruleForm dslb">
            <el-form-item label="姓名" prop="name">
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="邮箱" prop="mail">
              <el-input v-model="ruleForm.mail"></el-input>
            </el-form-item>
            <el-form-item label="颜色" prop=''>
              <input class="color_type" v-model="ruleForm.b_color" type='color'></input>
              <input class="color_type" v-model="ruleForm.c_color" type='color'></input>
            </el-form-item>
            <el-form-item label="密码" prop="psw">
              <el-input v-model="ruleForm.psw" type='password'></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="checkpsw">
              <el-input v-model="ruleForm.checkpsw" type='password'></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="sex" @keyup.enter.native="submitForm('ruleForm')">
              <el-radio-group v-model="ruleForm.sex">
                <el-radio label="男"></el-radio>
                <el-radio label="女"></el-radio>
                <el-radio label="未知"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" style="font-family:happy" class="btn" @click="submitForm('ruleForm')" >立即注册</el-button>
            </el-form-item>
          </el-form>
          <el-form :model="ruleForm_" @submit.native.prevent :rules="rules_" ref="ruleForm_" hide-required-asterisk label-width="100px" class="demo-ruleForm dslb">
            <el-form-item label="姓名/邮箱" prop="nameMail">
              <el-input v-model="ruleForm_.nameMail"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="psw">
              <el-input v-model="ruleForm_.psw" type='password'  @keyup.enter.native="submitForm_('ruleForm_')"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" style="font-family:happy" class="btn" @click="submitForm_('ruleForm_')">登陆</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
    <div class="txal mg-t-30" v-show='this.$store.state.isLogin'>
      <div class="f20 padd-25 title">个人信息</div>
      <div class="">
        <div class="padd-b-20">姓名：{{this.$store.state.user.name}}</div>
        <div class="padd-b-20">邮箱地址：{{this.$store.state.user.mail}}</div>
        <div class="padd-b-20">性别：{{this.$store.state.user.sex}}</div>
      </div>
      <el-button type="primary" style="font-family:happy" class="btn" @click="againOn">重新登陆</el-button>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.psw) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
    };
    var checkEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error('邮箱不能为空'))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error('请输入正确的邮箱格式'))
        }
      }, 100)
    }
    return {
      message:'login',
      toggle:'',
      ruleForm: {
        name: '',
        mail: '',
        b_color:'#ff0000',
        c_color:'#000000',
        psw: '',
        checkpsw:'',
        sex: '',
        nameMail:''
      },
      rules: {
        name: [
          { required: true, message: '请输入你的名字', trigger: 'blur' },
          { min: 3, max: 4, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        mail: { validator: checkEmail, trigger: 'blur' },
        psw: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 8, max: 8, message: '长度8个字符', trigger: 'blur' }
        ],
        checkpsw:{ validator: validatePass2, trigger: 'blur' },
        sex: [
          { required: true, message: '请选择性别', trigger: 'change' }
        ],
      },
      ruleForm_:{
        nameMail:'',
        psw: '',
      },
      rules_:{
        nameMail: [
          { required: true, message: '请填写登陆名或邮箱地址', trigger: 'blur' }
        ],
        psw: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 8, max: 8, message: '长度8个字符', trigger: 'blur' }
        ],
      }
    }
  },
  computed: {
    
  },
  watch: {
    
  },
  mounted(){
    
  },
  methods: {
    //注册
    submitForm(formName) {
      console.log(this.ruleForm)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let data={
            type:0,
            data:{
              name:this.ruleForm.name,
              mail:this.ruleForm.mail,
              psw:this.ruleForm.psw,
              sex:this.ruleForm.sex,
              b_color:this.ruleForm.b_color,
              c_color:this.ruleForm.c_color,
            }
          }
          this.$api.login(data,res=>{
            console.log(res)
            if(res.code==1000){
              this.$store.commit('updateLogin',true)
              this.$store.commit('updateUser',res.data)
              this.resetForm(formName)
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //登陆
    submitForm_(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let data={
            type:1,
            data:{
              nameMail:this.ruleForm_.nameMail,
              psw:this.ruleForm_.psw,
            }
          }
          this.$api.login(data,res=>{
            if(res.code==1000){
              this.$store.commit('updateLogin',true)
              this.$store.commit('updateUser',res.data)
              this.resetForm(formName)
            }
            console.log(res)
            
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    //清空
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //重新登陆
    againOn(){
      this.$store.commit('updateLogin',false)
      this.$store.commit('updateUser',{})
    },
  },
}
</script>

<style lang="less" scoped>
  #login_bigBox{
    color: aliceblue;
  }

  .btn{
    font-family: happy;
    background-image: linear-gradient(to right, #ff0000, #000000);
    border: none;
  }
  .btn_{
    font-family: happy;
    background-image: linear-gradient(to right, #000000,#ff0000,);
    border: none;
  }
  .title{
    color: #debdbd;
  }
  .color_type{
    background: white;
    border: none;
    border-radius: 4px;
  }

</style>



